import React from "react";

import { DrawerProps } from "../Drawer";

type DrawerContextInterface = Pick<DrawerProps, "onRequestClose" | "hideCloseButton">;

export const DrawerContext = React.createContext<DrawerContextInterface | null>(null);

export const useDrawerContext = (): DrawerContextInterface => {
	const context = React.useContext(DrawerContext);
	if (!context) {
		throw new Error("useDrawerContext must be used with DrawerContextProvider");
	}
	return context;
};
